import React from "react";

import { useTranslation } from "react-i18next";
// icons
import { CgWebsite } from "react-icons/cg";
import { IoLogoDesignernews } from "react-icons/io";
import { FaMobile } from "react-icons/fa";
// styles
import "./styles.css";
interface Props {}

const Services = (props: Props) => {
  // hooks
  const { t } = useTranslation();
  return (
    <div className="servicesMainCon " id="services">
      <h3 className="redText bolder">{t("Services.1")}</h3>
      <h1 className="lineHeightLess">{t("MyServices.1")} </h1>
      <h6>{t("Services1.1")}</h6>
      <div className="servicesSubCon">
        {/* starting service */}
        <div className="serviceCon hvr-grow">
          <div className="serviceIconCon">
            <CgWebsite size={45} color="#9a0e2a" />
          </div>
          <div className="serviceHeaderCon">
            <h2>{t("Webdevelopment.1")}</h2>
          </div>
          <div className="serviceBodyCon">
            <h6>{t("Webdevelopment1.1")}</h6>
          </div>
        </div>
        {/* end of one service */}
        {/* starting service */}
        <div className="serviceCon hvr-grow">
          <div className="serviceIconCon">
            <IoLogoDesignernews size={45} color="#9a0e2a" />
          </div>
          <div className="serviceHeaderCon">
            <h2>{t("WebDesign.1")}</h2>
          </div>
          <div className="serviceBodyCon">
            <h6>{t("WebDesign1.1")}</h6>
          </div>
        </div>
        {/* end of one service */}
        {/* starting service */}
        <div className="serviceCon hvr-grow">
          <div className="serviceIconCon">
            <FaMobile size={45} color="#9a0e2a" />
          </div>
          <div className="serviceHeaderCon">
            <h2>{t("Mobileapps.1")}</h2>
          </div>
          <div className="serviceBodyCon">
            <h6>{t("Mobileapps1.1")}</h6>
          </div>
        </div>
        {/* end of one service */}
      </div>
    </div>
  );
};

export default Services;
