import React, { useState, useEffect } from "react";

// icons
import { GiHamburgerMenu } from "react-icons/gi";
import { AiOutlineCloseCircle } from "react-icons/ai";
import i18next from "i18next";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";

// styles
import "./styles.css";
import { changeLang } from "../../store/actions";
// hooks
import useWindowsDimensions from "../../hooks/useWindowDimensions";

interface Props {}
const Navbar = (props: Props) => {
  // getting categories from redux store
  const { currentLanguage } = useSelector((state: any) => ({
    // categories: state.category.all,
    currentLanguage: state.language.langCode,
  }));
  // console.log("cur lang", currentLanguage);

  // hooks
  const { t } = useTranslation();
  const { width, height } = useWindowsDimensions();
  const dispatch = useDispatch();
  // states
  const [showNavbar, setShowNavbar] = useState(false);
  const [tabletMode, setTabletMode] = useState(false);
  const [selectedLang, setselectedLang] = useState<any>("En");
  // effects
  useEffect(() => {
    // console.log(`${width}x${height}`);
    if (width > 768) {
      setTabletMode(true);
    } else {
      setTabletMode(false);
    }
  }, [width, height]);
  // useEffect(() => {
  //   handleLanguageChange(selectedLang);
  // }, [selectedLang]);
  useEffect(() => {
    handleLanguageChange(currentLanguage);
  }, [currentLanguage]);
  // console.log("cur lang", currentLanguage);

  // funcs
  function handleLanguageChange(lang: any) {
    i18next.changeLanguage(lang);
  }
  const changeLocalLangCode = (languageCode: string) => {
    setselectedLang(
      languageCode.charAt(0).toUpperCase() + languageCode.slice(1)
    );
    dispatch(changeLang(languageCode));
  };

  return (
    <div
      className={
        tabletMode
          ? "tabletNavbarMainCon"
          : showNavbar
          ? "navbarMainCon"
          : "hiddenNavbarMainCon"
      }
    >
      <div
        className={
          tabletMode
            ? "tabletnavLeftCon"
            : showNavbar
            ? "navLeftCon"
            : "hiddenNavLeftCon"
        }
      >
        <a href="/">
          <img
            src={`https://img.icons8.com/emoji/48/000000/b-button-blood-type-emoji.png`}
            alt="logo"
          />
          {/* <img src="https://img.icons8.com/color/40/000000/b.png" /> */}
        </a>
      </div>
      <div
        className={
          tabletMode
            ? "tabletnavRightCon"
            : showNavbar
            ? "navRightCon"
            : "hiddenNavRightCon"
        }
      >
        {showNavbar || tabletMode ? (
          <>
            <div className="hvr-sink">
              <a href="/#home">
                <h6 className="text-decoration-none my-3">{t("Home.1")}</h6>
              </a>
            </div>
            <div className="hvr-sink">
              <a href="/#services">
                <h6 className="text-decoration-none my-3">{t("Services.1")}</h6>
              </a>
            </div>

            <div className="hvr-sink">
              <a href="/#skills">
                <h6 className="text-decoration-none my-3">{t("Skills.1")}</h6>
              </a>
            </div>
            <div className="hvr-sink">
              <a href="/#projects">
                <h6 className="text-decoration-none my-3">{t("Projects.1")}</h6>
              </a>
            </div>
            <div className="hvr-sink">
              <a href="/#contact">
                <h6 className="text-decoration-none my-3">{t("Contact.1")} </h6>
              </a>
            </div>
            <div className="btns-container">
              <div>
                <button
                  className={
                    selectedLang == "En"
                      ? "change-lang-btn active-lang-btn"
                      : "change-lang-btn"
                  }
                  // onClick={() => setselectedLang("en")}
                  onClick={() => changeLocalLangCode("en")}
                >
                  <img src="https://img.icons8.com/emoji/40/000000/united-kingdom-emoji.png" />
                </button>
              </div>
              <div>
                <button
                  className={
                    selectedLang == "Uz"
                      ? "change-lang-btn active-lang-btn"
                      : "change-lang-btn"
                  }
                  // onClick={() => setselectedLang("uz")}
                  onClick={() => changeLocalLangCode("uz")}
                >
                  <img src="https://img.icons8.com/emoji/40/000000/uzbekistan-emoji.png" />
                </button>
              </div>
              <div>
                <button
                  className={
                    selectedLang == "Ru"
                      ? "change-lang-btn active-lang-btn"
                      : "change-lang-btn"
                  }
                  // onClick={() => setselectedLang("ru")}
                  onClick={() => changeLocalLangCode("ru")}
                >
                  <img src="https://img.icons8.com/emoji/40/000000/russia-emoji.png" />
                </button>
              </div>
            </div>
            {!tabletMode && (
              <button onClick={() => setShowNavbar(false)} className="closeBtn">
                <AiOutlineCloseCircle size={25} color="white" />
              </button>
            )}
          </>
        ) : (
          <button onClick={() => setShowNavbar(true)} className="showBtn">
            <GiHamburgerMenu size={25} color="white" />
          </button>
        )}
      </div>
    </div>
  );
};

export default Navbar;
