import React from "react";

// @ts-ignore
import Lottie from "react-lottie";

// animation
import loadingAnim from "../../assets/animations/loadingAnim.json";

// @ts-ignore
const LottieCom = ({ anim }) => {
  return (
    <div>
      <Lottie
        // @ts-ignore
        options={{
          loop: true,
          autoplay: true,
          animationData: anim ? anim : loadingAnim,
          rendererSettings: {
            preserveAspectRatio: "xMidYMid slice",
          },
        }}
        height={400}
        width={400}
        isStopped={false}
        isPaused={false}
      />
    </div>
  );
};

export default LottieCom;
