import React, { useState } from "react";

import { useForm } from "react-hook-form";
// @ts-ignore
import { useHistory } from "react-router-dom";
import { useTranslation } from "react-i18next";

// icons
import { BiPhoneCall } from "react-icons/bi";
import { SiMailDotRu } from "react-icons/si";
import { HiOutlineLocationMarker } from "react-icons/hi";
// styles
import "./styles.css";
interface Props {}

const Contact = (props: Props) => {
  // hooks
  const history = useHistory();
  const { t } = useTranslation();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const onSubmit = (data: any) => history.push("/success");

  // states
  const [email, setemail] = useState("");
  const [textMsg, settextMsg] = useState("");

  return (
    <div className="contactMainCon" id="contact">
      <h3>{t("Contact.1")}</h3>
      <div className="contactSubCon">
        <div className="contactInfoCon">
          <h1>{t("Contact1.1")}</h1>
          <h6>{t("Hint2.1")}</h6>
          <div className="infoCon">
            <div>
              <BiPhoneCall color="white" size={30} />
            </div>
            <div>
              <h4>+998 91 669 31 04</h4>
            </div>
          </div>
          <div className="infoCon">
            <div>
              <SiMailDotRu color="white" size={30} />
            </div>
            <div>
              <h4>bakhr0mkhan@mail.ru</h4>
            </div>
          </div>
          <div className="infoCon">
            <div>
              <HiOutlineLocationMarker color="white" size={30} />
            </div>
            <div>
              <h4>4a, N45, Chilonzor 45, Toshkent, Uzbekistan</h4>
            </div>
          </div>
        </div>
        <div className="contactFormCon w-100 ">
          <h1>{t("Contact2.1")}</h1>

          <form onSubmit={handleSubmit(onSubmit)} className="contactForm w-100">
            <div className="mb-3 ">
              <label htmlFor="exampleFormControlInput1" className="form-label">
                Email
              </label>
              <input
                type="email"
                className="form-control"
                id="exampleFormControlInput1"
                placeholder="name@example.com"
                value={email}
                onChange={(e) => setemail(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <label
                htmlFor="exampleFormControlTextarea1"
                className="form-label"
              >
                {t("Message.1")}
              </label>
              <textarea
                className="form-control"
                id="exampleFormControlTextarea1"
                rows={3}
                value={textMsg}
                onChange={(e) => settextMsg(e.target.value)}
              ></textarea>
            </div>
            <div>
              <input
                type="submit"
                className="btn btn-danger btn-lg"
                disabled={!email || !textMsg}
              />
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
