import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  // @ts-ignore
} from "react-router-dom";
// bootstrap
import "bootstrap/dist/css/bootstrap.min.css";

// screens import
import { Dashboard, Project, PageNotFound, Success } from "./screens";
// components import
import { Navbar } from "./components";

const App = () => {
  return (
    <Router>
      <Navbar />
      <Switch>
        {/* <Route exact path="/stack/:stack">
          <ProjectsByStack />
        </Route> */}
        <Route exact path="/project/:id">
          <Project />
        </Route>
        <Route exact path="/success">
          <Success />
        </Route>
        <Route exact path="/">
          <Dashboard />
        </Route>
        {/* <Route path="*" component={PageNotFound} /> */}
      </Switch>
    </Router>
  );
};
export default App;
