import React, { useEffect, useState } from "react";

// @ts-ignore
import Lottie from "react-lottie";

// @ts-ignore
import { useHistory } from "react-router-dom";
// styles
import "./styles.css";
// animationFile
import loadingAnim from "../../assets/animations/loadingAnim.json";
import notFoundAnim from "../../assets/animations/notFound.json";
import { projects } from "../../assets/dummy";
import { useTranslation } from "react-i18next";

interface Props {}

const Project = (props: Props) => {
  // states
  const [loading, setLoading] = useState(false);
  // @ts-ignore
  const [project, setProject] = useState<any>();
  const [notFound, setNotFound] = useState(false);
  // hooks
  const history = useHistory();
  const { t } = useTranslation();
  // effects
  useEffect(() => {
    let prFound = projects.filter(
      (project) => project?.id === window.location.pathname.split("/").pop()
    )[0];
    if (prFound) {
      setProject(prFound);
    } else {
      setNotFound(true);
    }
  }, []);

  useEffect(() => {
    if (notFound) history.push("/notfound");
  }, [notFound]);

  return (
    <div className="projectMainCon">
      {loading && (
        <div className="projectSubCon">
          <div
            className="projectAnimCon"
            style={{
              marginTop: 10,
            }}
          >
            <Lottie
              options={{
                loop: true,
                autoplay: true,
                animationData: loadingAnim,
                rendererSettings: {
                  preserveAspectRatio: "xMidYMid slice",
                },
              }}
              height={400}
              width={400}
              isStopped={false}
              isPaused={false}
            />
          </div>
        </div>
      )}
      {notFound && (
        <div className="projectSubCon">
          <div
            className="projectAnimCon"
            style={{
              marginTop: 10,
            }}
          >
            <Lottie
              options={{
                loop: true,
                autoplay: true,
                animationData: notFoundAnim,
                rendererSettings: {
                  preserveAspectRatio: "xMidYMid slice",
                },
              }}
              height={250}
              width={250}
              isStopped={false}
              isPaused={false}
            />
          </div>
        </div>
      )}
      {project && !loading && !notFound && (
        <div className="projectSubCon">
          {/* @ts-ignore */}
          <h1>{project.name ? t(project.name) : "Project name"}</h1>
          {/* @ts-ignore */}
          <h6>{t(`${project?.description}.1`) || "Project description"}</h6>
          <div className="projectImgDiv">
            {/* @ts-ignore */}
            {project?.images.length ? (
              <div>
                {project?.images.map((prImg: any) => (
                  <img src={prImg} className="my-2" />
                ))}
              </div>
            ) : (
              <img src={project?.image} />
            )}
          </div>
          {project?.codeShown ? (
            <div className="projectActionBtns">
              {/* @ts-ignore */}
              <a
                href={`${project?.link || "google.com"}`}
                className="btn btn-danger btn-lg"
              >
                {t("Visit.1")}
              </a>
              {/* @ts-ignore */}
              <a
                href={`${project?.github || `github.com`}`}
                className="btn btn-danger btn-lg"
              >
                Github
              </a>
            </div>
          ) : (
            <div></div>
          )}

          <h1>{t("TechStack.1")}</h1>
          <div className="projectsStackCon">
            {/* @ts-ignore */}
            {project.stack &&
              // @ts-ignore
              project.stack.map((stack) => (
                // @ts-ignore
                <div className="projectStackCon">
                  <div className="projectStackHeader">
                    {/* @ts-ignore */}
                    <img src={stack.icon} />
                  </div>
                  <div className="projectStackBody">
                    {/* @ts-ignore */}
                    <h6>{stack.name}</h6>
                  </div>
                </div>
              ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Project;
